import {Button, IconButton, Menu, MenuItem} from "@mui/material";
import { Link } from "react-router-dom";
import DeleteIcon from '@mui/icons-material/Delete';
import SettingsIcon from '@mui/icons-material/Settings';
import BookmarkBorderIcon from '@mui/icons-material/BookmarkBorder';
import BookmarkIcon from '@mui/icons-material/Bookmark';
import Snackbar from '@mui/material/Snackbar';
import Fade from '@mui/material/Fade';
import {useState} from "react";
import CustomAlert from "./modal/CustomAlert";
import { CopyToClipboard } from "react-copy-to-clipboard";
import CustomConfirm from "./modal/CustomConfirm";

export default function BookCard(props) {
    // snack state
    const [state, setState] = useState({
        open: false,
        Transition: Fade,
    });

    // snackMessage
    const [snackMessage, setSnackMessage] = useState("");

    // alert state
    const [alertVisible, setAlertVisible] = useState(false);

    // setting menu state
    const [anchorEl, setAnchorEl] = useState(null);
    const open = Boolean(anchorEl);

    // confirm state
    const [confirmVisible, setConfirmVisible] = useState(false);

    /**
     * @description :      
     * */
    const clickBtn1 = () => {
        setConfirmVisible(false);
    };

    /**
     * @description :      
     * @TODO :    
     * */
    const clickBtn2 = () => {
        setConfirmVisible(false);
    };

    /**
     * @description :    
     * */
    const handleBookmarkClick = () => () => {
        if(props.cardType === 'A'){
            //   
            props.updateBookmark();
            // snack message 
            setSnackMessage(props.isBookmark ? " ." : " .");
            // snack  
            setState({
                open: true,
                Transition: Fade,
            });
        }
    };

    /**
     * @description :    
     * */
    const handleCopy = () => () => {
        // snack message 
        setSnackMessage(" .");
        // snack  
        setState({
            open: true,
            Transition: Fade,
        });
    };

    /**
     * @description :    
     * */
    const handleSnackClose = () => {
        setState({
            ...state,
            open: false,
        });
    };

    /**
     * @description : Alert  
     * */
    const openAlert = () => {
        setAlertVisible(true);
    };

    /**
     * @description : Alert  
     * */
    const closeAlert = () => {
        setAlertVisible(false);
    };

    /**
     * @description : setting  
     * */
    const handleSettingClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    
    /**
     * @description : setting  
     * */
    const handleSettingClose = () => {
    setAnchorEl(null);
    };

    
    /**
     * @description : Confirm  
     * */
    const openConfirm = () => {
        setConfirmVisible(true);
    };


    return (
        <>
            {/*   */}
            <Snackbar
                open={state.open}
                onClose={handleSnackClose}
                TransitionComponent={state.Transition}
                message={snackMessage}
                key={state.Transition.name}
                autoHideDuration={1200}
            />
            
            {/*   Alert */}
            <CustomAlert
                title={props.title+"   ."}
                content={         
                <CopyToClipboard
                    text={props.bookUrl}
                    onCopy={handleCopy(Fade)}>
                    <button>   : {props.bookUrl}</button>
                </CopyToClipboard>}
                openAlert={alertVisible}
                closeAlert={closeAlert}
            />

            {/* Setting     */}
            <Menu
                id="basic-menu"
                anchorEl={anchorEl}
                open={open}
                onClose={handleSettingClose}
                MenuListProps={{
                'aria-labelledby': 'basic-button',
                }}>
                <MenuItem onClick={handleSettingClose}> </MenuItem>
                <MenuItem onClick={handleSettingClose}> PDF </MenuItem>
                <MenuItem onClick={handleSettingClose}></MenuItem>
                <MenuItem onClick={handleSettingClose}></MenuItem>
            </Menu>

            {/* Setting     */}       
            <CustomConfirm
                id={12}
                openConfirm={confirmVisible}
                clickBtn1={clickBtn1}
                clickBtn2={clickBtn2}
            />
            <div className="rounded-lg border bg-card text-card-foreground shadow-sm w-full" data-v0-t="card">
                <Link>
                    <img src={props.photo} alt="" style={{width:'60%', margin:'auto', display:'block'}} className="w-full h-48 rounded-t"/>
                </Link>
                
                <div className="p-4">
                    <Link>
                        <div className="inline-flex w-fit items-center whitespace-nowrap rounded-full border px-2.5 py-0.5 text-xs font-semibold transition-colors focus:outline-none focus:ring-2 focus:ring-ring focus:ring-offset-2 border-transparent bg-secondary text-secondary-foreground hover:bg-secondary/80"
                            data-v0-t="badge">
                            {props.nickname}  {props.createDate}
                        </div>
                        <h3 className="mt-2 text-lg font-bold">{props.title}</h3>
                        <p className="mt-1 text-sm text-gray-600">{props.category}</p>
                        <p className="mt-1 text-sm text-gray-600"> {props.viewCount} |  {props.questionCount} |  {props.sectionCount}</p>
                        <p className="mt-1 text-sm text-gray-600">{props.status}</p>
                    </Link>

                    {/* A -   ,  ,   -   , */
                        props.cardType === 'A' ?
                            <div className="flex items-center justify-between mt-4">
                                <IconButton className="text-red-600" 
                                            onClick={handleBookmarkClick()}>
                                    { props.isBookmark ? <BookmarkIcon/> : <BookmarkBorderIcon/> }
                                </IconButton>
                                <Button className="px-4 py-2 text-gray-600 border border-gray-600 rounded"
                                        onClick={openAlert}></Button>
                            </div> : ""
                    }

                    { /* B -   -   ,   */
                        props.cardType === 'B' ?
                            <div className="flex items-center justify-between mt-4">
                                <IconButton className="text-red-600"
                                            onClick={handleSettingClick}>
                                    <SettingsIcon/>
                                </IconButton>
                                <Button className="px-4 py-2 text-gray-600 border border-gray-rounded"
                                        onClick={openAlert}></Button>
                            </div> : ""
                    }
                    {/* C -  */
                        props.cardType === 'C' ?
                        <div className="flex items-center justify-between mt-4">
                            <IconButton className="text-red-600"
                                        onClick={openConfirm}>
                                <DeleteIcon/>
                            </IconButton>
                            <Button className="px-4 py-2 text-gray-600 border border-gray-600 rounded">
                                <Link to={props.bookUrl}> </Link>
                            </Button>
                        </div>  : ""
                    }
                </div>
            </div>

        </>

    )
}