import React, { useEffect, useState } from 'react';
import { Button, IconButton, TextField, Typography } from "@mui/material";
import VisibilityIcon from '@mui/icons-material/Visibility';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';

export default function VarificationInput(props) {
    const [timeLeft, setTimeLeft] = useState(180); // 180 = 3

    //   
    useEffect(() => {
        if (props.timerActive && timeLeft > 0) {
            const timer = setTimeout(() => setTimeLeft(timeLeft - 1), 1000);
            return () => clearTimeout(timer);
        } else if (timeLeft === 0) {
            props.onTimerEnd && props.onTimerEnd(); //     
        }
    }, [timeLeft, props.timerActive]);

    //  MM:SS   
    const formatTime = (seconds) => {
        const minutes = Math.floor(seconds / 60);
        const remainingSeconds = seconds % 60;
        return `${minutes.toString().padStart(2, '0')}:${remainingSeconds.toString().padStart(2, '0')}`;
    }

    const startTimer = () => {
        setTimeLeft(180); //  
        props.onStartTimer(); //     
    }

    return (
        <div className="flex flex-col">
            <div className="flex items-center">
                <TextField id="standard-basic"
                           label={props.label}
                           value={props.value}
                           type={"email"}
                           variant="standard"
                           placeholder={props.placeholder}
                           onChange={(e) => props.updateValue(e.target.value)}
                />
                <div>
                    <Button variant="contained" onClick={startTimer}></Button>
                </div>
            </div>

            <Typography variant="caption" color={props.captionColor}>
                  : {formatTime(timeLeft)}
            </Typography>
        </div>
    );
}
