import { useState } from 'react';
import { DndProvider, useDrag, useDrop } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';
import DragIndicatorIcon from '@mui/icons-material/DragIndicator';
import ArrowUpwardIcon from '@mui/icons-material/ArrowUpward';
import ArrowDownwardIcon from '@mui/icons-material/ArrowDownward';
import { IconButton } from '@mui/material';

//       .
const ItemType = 'ROW';

//    ,     .
function DraggableRow({ row, index, moveRow, arrLength }) {
    // useDrag       .
    const [, ref] = useDrag({
        type: ItemType,
        item: { index },
    });

    // useDrop        .
    const [, drop] = useDrop({
        accept: ItemType,
        hover: (draggedItem) => {
            if (draggedItem.index !== index) {
                moveRow(draggedItem.index, index);
                draggedItem.index = index;
            }
        },
    });

    return (
        <div
            ref={(node) => ref(drop(node))}
            key={row.id}
            className="flex items-center justify-between p-2 border rounded-md"
        >
            <div className="flex items-center space-x-2">
                <IconButton>
                    <DragIndicatorIcon /> {/*    */}
                </IconButton>
                <div>
                    <div>{row.title || ' '}</div>
                    <div className="text-sm text-muted-foreground">
                        {index + 1}  / {arrLength} 
                    </div>
                </div>
            </div>
            <div className="flex space-x-2">
                {/*    */}
                <IconButton
                    onClick={() => moveRow(index, index - 1)}
                    disabled={index === 0} //    
                >
                    <ArrowUpwardIcon />
                </IconButton>

                {/*    */}
                <IconButton
                    onClick={() => moveRow(index, index + 1)}
                    disabled={index === arrLength - 1} //   
                >
                    <ArrowDownwardIcon />
                </IconButton>
            </div>
        </div>
    );
}

//   ,       .
export default function SectionSort({ sortData }) {
    const [rows, setRows] = useState(sortData); //       .

    //    .
    const moveRow = (fromIndex, toIndex) => {
        // toIndex     .
        if (toIndex < 0 || toIndex >= rows.length) return;

        const updatedRows = [...rows];
        const [movedItem] = updatedRows.splice(fromIndex, 1);
        updatedRows.splice(toIndex, 0, movedItem);
        setRows(updatedRows); //     .
    };

    return (
        <DndProvider backend={HTML5Backend}>
            <div className="space-y-4 min-w-[400px]">
                {rows.map((row, index) => (
                    <DraggableRow
                        key={row.id}
                        row={row}
                        index={index}
                        moveRow={moveRow}
                        arrLength={rows.length}
                    />
                ))}
            </div>
        </DndProvider>
    );
}
