import CustomConfirm from "../../components/modal/CustomConfirm";
import React, {useEffect, useState} from "react";
import {
  Button,
  MenuItem, Pagination,
  Stack,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  TextField
} from "@mui/material";
import NewClass from "../../components/modal/NewClass";
import axios from "axios";
import CustomAlert from "../../components/modal/CustomAlert";
import Paper from '@mui/material/Paper';

const conditions = [
  {
    value: 'popular',
    label: '',
  },
  {
    value: 'recent',
    label: '',
  },
  {
    value: 'old',
    label: '',
  },
  {
    value: 'title',
    label: '',
  },
];

const ITEMS_PER_PAGE = 10;
const SPACING = 2;

export default function Myclass() {

  const [alertVisible, setAlertVisible] = useState(false);
  const [confirmVisible, setConfirmVisible] = useState(false);
  const [className, setClassName] = useState("");
  const [classDescription, setClassDescription] = useState("");
  const [alertTitle, setAlertTitle] = useState("");
  const [classList,setClassList]=useState([]);
  const onSetClassName = (className) => {
    setClassName(className);
  }

  const onSetClassDescription = (classDescription) => {
    setClassDescription(classDescription);
  }

  //    
  useEffect(()=>{
    getDataList();
  },[]);

  const getDataList=()=>{
      axios({
        method:'get',
        url:'/myclass/list',
      }).then(res=>{
        console.log(res);
        setClassList(res.data);
      })
  }

  /**
   * @description : Confirm  
   * */
  const openConfirm = () => {
    setConfirmVisible(true);
  };

  /**
   * @description : Alert  
   * */
  const openAlert = () => {
    setAlertVisible(true);
  };

  /**
   * @description : Alert  
   * */
  const closeAlert = () => {
    setAlertVisible(false);
  };

  /**
   * @description :     
   * */
  const clickBtn1 = () => {
    setConfirmVisible(false);
  };

  /**
   * @description :     
   * */
  const clickBtn2 = () => {
    if(className==''){
      setAlertTitle("  .");
      openAlert();
      return;
    }
    if(classDescription==''){
      setAlertTitle("  .");
      openAlert();
      return;
    }
    axios({
      method:'post',
      url:'/myclass/newclass',
      data:{
        "class_name": className,
        "class_description": classDescription,
      },
    }).then(res=>{
      console.log(res);
      setClassName('');
      setClassDescription('');
      setAlertTitle("  .");
      openAlert();
      getDataList(); //   
    })
    setConfirmVisible(false);
  };

  const [page, setPage] = useState(1);

  const handleChange = (event, value) => {
    setPage(value);
    window.scrollTo(0, 0);
  };

  const itemOffset = (page - 1) * ITEMS_PER_PAGE;
  const currentItems = classList.slice(itemOffset, itemOffset + ITEMS_PER_PAGE);
  const pageCount = Math.ceil(classList.length / ITEMS_PER_PAGE);


  return (
      <main className="flex-1 p-6">

        <h1 className="mb-6 text-2xl font-bold"> </h1>
        <div className="flex items-center mb-4 space-x-4">
          <TextField
              id="outlined-select-currency"
              select
              defaultValue="popular"
          >
            {conditions &&
                conditions.map((option) => (
                <MenuItem key={option.value} value={option.value}>
                  {option.label}
                </MenuItem>
            ))}
          </TextField>
          <Button
              onClick={openConfirm}
              variant="contained"
              className="whitespace-nowrap">
             
          </Button>
          <CustomConfirm
              id={9} // CustomConfirm id  
              // props   (title, content, btn1Text,  btn2Text)
              content={
                <NewClass
                    label={"Class name"}
                    placeholder={""}
                    updateClassName={onSetClassName}
                    updateClassDescription={onSetClassDescription}
                />
              }
              openConfirm={confirmVisible}
              clickBtn1={clickBtn1}
              clickBtn2={clickBtn2}
          />
          <CustomAlert
              //id={5} // CustomAlert id  
              // props   (title, content, btnText)
              title={alertTitle}
              openAlert={alertVisible}
              closeAlert={closeAlert}
          />
        </div>
        <div
            className="rounded-lg border bg-card text-card-foreground shadow-sm"
            data-v0-t="card"
        >
          <TableContainer component={Paper}>
            <Table sx={{minWidth: 650}} aria-label="simple table">
              <TableHead>
                <TableRow>
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {currentItems &&
                    currentItems.map((row) => (
                        <TableRow
                            key={row.class_id}
                            sx={{'&:last-child td, &:last-child th': {border: 0}}}
                        >
                          <TableCell component="th" scope="row">
                            {row.class_name}
                          </TableCell>
                          <TableCell>{row.class_description}</TableCell>
                          <TableCell>{row.class_createdate}</TableCell>
                          <TableCell>{row.carbs}</TableCell>
                          <TableCell>{row.protein}</TableCell>
                        </TableRow>
                    ))
                }
              </TableBody>
            </Table>
          </TableContainer>
        </div>
        <div className={"flex justify-center mt-4"}>
          <Stack spacing={SPACING}>
            <Pagination
                count={pageCount}
                page={page}
                onChange={handleChange}
                showFirstButton
                showLastButton
            />
          </Stack>
        </div>
      </main>
);
}
