import { useState } from "react";
import {
  Typography,
  Button,
  Container,
  Card,
  CardContent,
  CardMedia,
  Avatar,
  Box,
  Grid,
  Chip,
} from "@mui/material";
import { ExpandMore } from "@mui/icons-material";

export default function Detail() {
  const [showMoreReviews, setShowMoreReviews] = useState(false);

  const toggleMoreReviews = () => {
    setShowMoreReviews(!showMoreReviews);
  };

  return (
      <Box sx={{ minHeight: "100vh", display: "flex", flexDirection: "column" }}>
        <Container component="main" sx={{ mt: 8, flexGrow: 1 }}>
          <Grid container spacing={1} justifyContent="center">
            <Grid item xs={12} md={6}>
              <Card>
                <CardMedia
                    component="img"
                    image="/placeholder.svg"
                    alt="Main Image"
                    sx={{ height: 300 }}
                />
                <CardContent>
                  <Typography variant="h6" component="div">
                    []
                  </Typography>
                  <Typography variant="h5" component="div">
                             
                     
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                      |  180 |  2024.07.18
                  </Typography>
                  <Box sx={{ mt: 2 }}>
                    <Typography variant="body2" component="p">
                            
                            
                           
                           
                           
                           
                           
                           
                           
                           
                      
                    </Typography>
                  </Box>
                  <Box sx={{ mt: 2, display: "flex", gap: 1 }}>
                    <Chip label="" color="primary" />
                    <Typography>100 </Typography>
                    <Chip label="" color="primary" />
                    <Typography>10 </Typography>
                    <Chip label=" " color="primary" />
                    <Typography>60(   )</Typography>
                  </Box>
                  <Button
                      variant="contained"
                      color="primary"
                      sx={{ mt: 2 }}
                      fullWidth
                  >
                     
                  </Button>
                </CardContent>
              </Card>
            </Grid>
          </Grid>
        </Container>
        <Container component="section" sx={{ mt: 4, mb: 4 }}>
          <Typography variant="h6" component="div">
             (4) <span style={{ color: "yellow" }}> 2.5</span>
          </Typography>
          <Box>
            {[
              {
                id: 1,
                reviewer: "1",
                date: "2024.06.19",
                content:
                    " .          .  ,              .          .  ,              .          .  ,             ",
              },
              {
                id: 2,
                reviewer: "2",
                date: "2024.06.19",
                content:
                    " .       !!!   ,      .",
              },
              {
                id: 3,
                reviewer: "3",
                date: "2024.06.19",
                content:
                    " .       !!!",
              },
              {
                id: 4,
                reviewer: "4",
                date: "2024.06.19",
                content:
                    " .       !!!       .",
              },
            ]
                .slice(0, showMoreReviews ? undefined : 2)
                .map((review) => (
                    <Card key={review.id} sx={{ mb: 2 }}>
                      <CardContent sx={{ padding: 2 }}>
                        <Box sx={{ display: "flex", alignItems: "center", mb: 2 }}>
                          <Avatar
                              alt="Reviewer Avatar"
                              src="/placeholder-user.jpg"
                              sx={{ width: 40, height: 40 }}
                          />
                          <Box sx={{ ml: 2 }}>
                            <Typography variant="body2" fontWeight="bold">
                              {review.reviewer}
                            </Typography>
                            <Typography variant="caption" color="text.secondary">
                              {review.date}
                            </Typography>
                          </Box>
                        </Box>
                        <Typography variant="body2">
                          {review.content}
                        </Typography>
                      </CardContent>
                    </Card>
                ))}
            <Button
                onClick={toggleMoreReviews}
                sx={{ mt: 2 }}
                startIcon={<ExpandMore />}
            >
              {showMoreReviews ? "" : ""}
            </Button>
          </Box>
        </Container>
      </Box>
  );
}
